* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  height: 100%;
}
body {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(
    90deg,
    rgb(14, 202, 161) 0%,
    rgb(179, 184, 118) 44%,
    rgba(95, 219, 45, 0.445) 100%
  );
}
#drum-machine {
  width: 350px;
  height: 400px;
  border: 1px solid rgba(5, 63, 50, 0.212);
  box-shadow: 5px 5px 5px black;
  position: relative;
  margin: auto;
}
#display {
  border: 1px solid black;
  position: absolute;
  bottom: 0;
  right: 0;
  margin-bottom: 35px;
  margin-right: 180px;
  border-radius: 50px 50px 50px 50px;
}
#display-bank {
  border: 1px solid black;
  position: absolute;
  bottom: 0;
  right: 0;
  margin-bottom: 35px;
  margin-right: 20px;
  border-radius: 50px 50px 50px 50px;
}
.display {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  width: 150px;
  padding-left: 5px;
}
h1 {
  position: absolute;
  top: 0;
  margin-top: 20px;
  text-align: center;
}
#drums {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin: 5px;
}
.pad-bank {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
.drum-pad {
  width: 25%;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 40%;
  font-weight: bolder;
  background: linear-gradient(
    270deg,
    rgb(14, 202, 161, 0.445) 0%,
    rgb(179, 184, 118) 44%,
    rgba(95, 219, 45, 0.445) 100%
  );
  border: 1px solid rgba(5, 63, 50, 0.212);
}
#power-control {
  border: 1px solid black;
  position: absolute;
  bottom: 0;
  right: 0;
  margin-bottom: 75px;
  margin-right: 180px;
  text-align: center;
  cursor: pointer;
  border-radius: 50px 50px 50px 50px;
}
#power-control:active {
  background-color: rgba(154, 241, 13, 0.151);
}
#bank-control {
  border: 1px solid black;
  position: absolute;
  bottom: 0;
  right: 0;
  margin-bottom: 75px;
  margin-right: 20px;
  text-align: center;
  cursor: pointer;
  border-radius: 50px 50px 50px 50px;
}
#bank-control:active {
  background-color: rgba(241, 237, 13, 0.507);
}
.volume-slider {
  position: absolute;
  bottom: 0;
  left: 0;
  margin-left: 125px;
  margin-bottom: 10px;
  z-index: 1000;
}

input[type="range"] {
  width: 100px;
  margin: 0;
  padding: 0;
  display: block;
  height: 10px;
  background: transparent;
  overflow: hidden;
  -webkit-appearance: none;
  background-color: #2abe32c4;
}
input[type="range"]:hover {
  cursor: grab;
}
